@import 'variables';

// HERO STYLES //
#hero {
    background-image: linear-gradient(to bottom, rgba(0,0,0,0.3), rgba(0,0,0,0.5)),url('../assets/photo1.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    background-attachment: fixed;
    filter: brightness(120%);
    min-height: 90vh;
    .container {
        scroll-behavior: smooth;
        .hero-content {
            margin: 0;
            position: absolute;
            top: 52%;
            left: 50%;
            transform: translate(-50%, -50%);
            text-align: center;
            h1 {
                font-size: 4.5rem;
                color: $primary-color;
                font-weight: 700;
            }
    
            h3 {
                font-size: 2.9rem;
                color: $light-color;
                margin: 1rem 0;
            }
            
            button {
                    @include button(1.4rem, 15.1rem, 4.5rem, 600);
    
                }
            
           
        }
       
    }

}
